import { Link } from 'gatsby'
import React from 'react'
import Helmet from 'react-helmet'
import { Waypoint } from 'react-waypoint'
import carlosPic from '../assets/images/team/carlos.jpeg'
import gioelePic from '../assets/images/team/gioele.jpg'
import nicolasPic from '../assets/images/team/nicolas.jpg'
import Header from '../components/Header'
import Layout from '../components/layout'
import Nav from '../components/Nav'
import ContactForm from '../components/ContactForm'

class Index extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      stickyNav: false,
    }
  }

  _handleWaypointEnter = () => {
    this.setState(() => ({ stickyNav: false }))
  }

  _handleWaypointLeave = () => {
    this.setState(() => ({ stickyNav: true }))
  }

  render() {
    return (
      <Layout>
        <Helmet title="Dolmy24 - Dolmetscher 24/7" />

        <Header />

        <Waypoint
          onEnter={this._handleWaypointEnter}
          onLeave={this._handleWaypointLeave}
        ></Waypoint>
        <Nav sticky={this.state.stickyNav} />

        <div id="main">

          <section id="intro" className="main special">
            <header className="major">
              <h2>Mache mehr mit weniger Clicks</h2>
            </header>
            <ul className="features">
              <li>
                <span className="icon major style5 fa-hourglass"></span>
                <h3>Einfache Buchungen</h3>
              </li>
              <li>
                <span className="icon major style5 fa-calendar"></span>
                <h3>berblick von Verfgbarkeiten</h3>
              </li>
              <li>
                <span className="icon major style5 fa-copy"></span>
                <h3>Konsolidierte Vertrge und Rechnungen</h3>
              </li>
            </ul>
            <iframe width="100%" height="500px" src="https://www.youtube-nocookie.com/embed/pWUZttD1gdU" frameborder="0" allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture" allowfullscreen></iframe>
          </section>

          <section id="team" className="main special">
            <header className="major">
              <h2>Team</h2>
            </header>
            <ul className="features">
              <li>
                <span className="image"><img src={nicolasPic} alt="" style={{ borderRadius: `100%`, height: "13em" }} /></span>
                <h3>Nicolas Berlinger</h3>
              </li>
              <li>
              <span className="image"><img src={gioelePic} alt="" style={{ borderRadius: `100%`, height: "13em" }} /></span>
                <h3>Gioele Moretti</h3>
              </li>
              <li>
              <span className="image"><img src={carlosPic} alt="" style={{ borderRadius: `100%`, height: "13em" }} /></span>
                <h3>Carlos Chida</h3>
              </li>
            </ul>
          </section>

          <section id="contact" className="main special">
            <header className="major">
              <h2>Contact</h2>
            </header>
            <ContactForm />
          </section>
        </div>
      </Layout>
    )
  }
}

export default Index
