import React from 'react'

import logo from '../assets/images/logo.svg';

const Header = (props) => (
    <header id="header" className="alt">
        <span className="logo"><img src={logo} alt="" /></span>
        <h1><b>Finde deinen Dolmetscher 24/7</b></h1>
    </header>
)

export default Header
